import { useEffect, useState } from 'react';
import { appInsights } from './analytics';
import io from 'socket.io-client';

// const baseUrl = 'http://localhost:3101';
const baseUrl = process.env.REACT_APP_BACK_END_API;
export const socket = io(baseUrl, { transports: ['websocket'] });

function App() {
  const [messages, setMessages] = useState([]);
  const [text, setText] = useState('');
  const [rate, setRate] = useState(10);
  const [load, setLoad] = useState(10);
  const [start, setStart] = useState(false);

  useEffect(() => {
    socket.on('update', dbMessages => setMessages(dbMessages));

    return () => socket.removeAllListeners('update');
  }, []);

  useEffect(() => {
    let timer;
    if (start)
      timer = setInterval(() => {
        fetch(baseUrl, {
          method: 'PATCH',
          body: JSON.stringify({ roomId: 'NoExist', msg: 'bombing' }),
          headers: { 'Content-type': 'application/json' },
        });
      }, rate);

    return () => clearInterval(timer);
  }, [rate, start]);

  const reset = async () => {
    appInsights.trackEvent({
      name: 'RESETING',
      properties: { 'msg!!!!!!': 1729 },
    });
    setText('');

    try {
      const response = await fetch(baseUrl, {
        method: 'POST',
        body: JSON.stringify({ roomId: 'minimal' }),
        headers: { 'Content-type': 'application/json' },
      });
      await response.json();
      if (!response.ok) throw new Error('something went wrong');
    } catch (error) {
      console.log(error);
    }
  };
  const inputHandler = async e => {
    setText(e.target.value);
    try {
      const response = await fetch(baseUrl, {
        method: 'PATCH',
        body: JSON.stringify({ roomId: 'minimal', msg: e.target.value }),
        headers: { 'Content-type': 'application/json' },
      });
      // const data = await response.json();
      if (!response.ok) throw new Error('something went wrong');
    } catch (error) {
      console.log(error);
    }
  };

  const cpuIntenese = () => {
    appInsights.trackMetric({ name: 'CpuIntense', average: 200 });
    fetch(baseUrl, {
      method: 'PATCH',
      body: JSON.stringify({ roomId: 'cpuLoad', load }),
      headers: { 'Content-type': 'application/json' },
    });
  };

  return (
    <div className="app">
      <div>
        {messages.map((msg, i) => (
          <div key={i}>{msg}</div>
        ))}
      </div>
      <input
        type="text"
        className="form-control"
        value={text}
        onChange={inputHandler}
      />
      <button className="btn" onClick={reset}>
        Reset
      </button>
      <div style={{ display: 'flex', flexDirection: 'column' }}>
        <button
          className="btn"
          style={{ backgroundColor: 'green' }}
          onClick={() => setStart(true)}>
          SenRequestPerMs
        </button>
        <input
          type="text"
          className="form-control"
          value={rate}
          onChange={e => setRate(e.target.value)}
        />
        <button
          className="btn"
          style={{ backgroundColor: 'red' }}
          onClick={() => setStart(false)}>
          Stop
        </button>
      </div>
      <div style={{ display: 'flex', flexDirection: 'column' }}>
        <button
          className="btn"
          style={{ backgroundColor: 'red' }}
          onClick={cpuIntenese}>
          CpuLoad
        </button>
        <input
          type="text"
          className="form-control"
          value={load}
          onChange={e => setLoad(e.target.value)}
        />
      </div>
    </div>
  );
}

export default App;
